.header{
    background: linear-gradient(90deg,rgba(73,63,252,1) 0% ,rgba(73,63,252,1) 100%);
    background: black;
    position: fixed;
    top: 0px;
    width: -webkit-fill-available;
    z-index: 10;
}
.header.active{
    background: black;
    background: linear-gradient(90deg,rgba(73,63,252,1) 0% ,rgba(73,63,252,1) 100%);
    background: linear-gradient(0deg, rgb(23, 31, 43), rgb(14, 7, 7));    color: bisque;

